.cdaka {
  &__btn {
    display: flex;
    justify-content: center;
    &>div {
      &+div {
        padding-left: 50px; } } }
  &__img {
    position: absolute;
    margin-left: -185px;
    margin-top: -20px;
    img {
      height: 189px;
      width: 100%;
