            margin-bottom: 20px;
            left: inherit;
            bottom: inherit;
            strong {
              display: block; }
            &:last-child {
              margin-bottom: 0; } } }
        &-btn {
          height: 88px;
          line-height: 88px;
          border-width: 3px;
